import React, { useState } from 'react'
import './HomePopup.scss';
import FlairButton from '../../componants/flairButton/FlairButton';
import { flairIcon } from '../../icons/icons.js';
import Stepper from '../../componants/stepper/Stepper.js'



function HomePopup(props) {

  const [step, setStep] = useState(0)



  const  StepDescritpion = ()=> {
    switch (step) {
      case 0: {
        return <p>Flair est une application qui vous permet d'investir  l'aide  d'une carte de l'le-de-France en constante mutation. <br/><br/> Elle analyse et trouve pour vous les villes pour lesquelles linvestissement sera le plus rentable.</p>
      }
      case 1: {
        return <p>Les informations que vous trouverez proviennent des donnes dmographiques, des tendances du march et des futurs projets damnagement urbain. <br/> <br/>
        Ces informations passent par notre algorithme, qui calcule pour vous les zones avec les meilleures rentabilits.</p>
      }
      case 2: {
        return <p>Chaque ville possde un pourcentage de rentabilit. Vous pouvez accder aux informations dtailles par ville pour comprendre plus en profondeur la provenance de nos rsultats.<br/><br/> Bon Flairage !</p>
       
      }
      default: {
        console.log(step, 'step ');
        return null;
      }
    }
  }



  return (

    <div className="popupWindow">
      <div className="popup">
        <div className="logoSpace">
          <img className='flairLogo' src={flairIcon}></img>
        </div>
        <Stepper steps={3} activeStep={step}></Stepper>
        <h2>Bienvenue sur Flair !</h2>

        <div className="description">
          <StepDescritpion />
        </div>

        <div className="buttonSpace">
          <FlairButton  className={step===0 ? 'disabled' : 'secondary'} onClick={() => step > 0 ? setStep(step-1) : null}>Retour</FlairButton>
          <FlairButton className="primary" onClick={() => step === 2 ? props.goToNav() : setStep(step+1)}>{step === 2 ? 'Commencer !' : 'Suivant'}</FlairButton>
        </div>


        <div className="prevention">
          Attention : Notre outil vous aiguille mais puisquil sagit de prdictions, et malgr la forte fiabilit de notre algorithme, nous ne pouvons pas garantir 100% de fiabilit.
      </div>
      </div>

    </div>
  );
}
export default HomePopup;